@charset "UTF-8";
/* font-weight */
fieldset,
legend,
button {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::placeholder {
  color: rgba(51, 51, 51, 0.4);
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
  margin: 0;
  padding: 4px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 16px;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
input[type="datetime"],
input[type="week"],
textarea,
select {
  padding: 0.25rem;
  border: 1px solid #ccc;
  border-radius: 0;
  outline: none;
  background: #fff;
  font-size: 1rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
}

textarea {
  width: 100%;
  height: 100px;
  overflow: auto;
}

select {
  padding-right: 30px;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-position: right 8px center;
}

.select {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
}

.select select {
  width: 100%;
  padding-right: 24px;
  border: 1px solid #ccc;
  background: none;
  text-overflow: "";
  cursor: pointer;
}

.select::before {
  position: absolute;
  top: 13px;
  right: 8px;
  width: 0;
  height: 0;
  padding: 0;
  content: "";
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #666;
  pointer-events: none;
}

/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="radio"] + span,
input[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  margin-left: 8px;
  padding: 10px 20px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.125rem;
  text-align: center;
  line-height: 1;
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  input[type="radio"] + span,
  input[type="checkbox"] + span {
    font-size: 1.5rem;
  }
}

input[type="radio"] + span::before,
input[type="checkbox"] + span::before {
  position: absolute;
  top: 50%;
  left: -8px;
  width: 16px;
  height: 16px;
  content: "";
  margin-top: -8px;
  border: 1px solid #ccc;
  background: #fff;
}

/* fieldset */
fieldset {
  padding: 8px 16px;
  border: 1px solid #ccc;
}

legend {
  padding: 0 8px;
}

/* button */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  display: inline-block;
  margin: 0;
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  background: #fff;
  color: #000;
  font-size: 1.25rem;
  cursor: pointer;
  transition: .3s;
}

/* ブラウザ対策
---------------------------------------------------------------------------- */
/* Firefox */
/* IE */
select::-ms-expand {
  display: none;
}

/* webkit */
/* iOS */
input[type="submit"]::-webkit-search-decoration,
input[type=reset]::-webkit-search-decoration,
input[type="button"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration {
  display: none;
}

::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  -moz-appearance: none;
  -webkit-appearance: textfield;
  -webkit-box-sizing: border-box;
  appearance: none;
  background: none;
  outline: none;
  font-size: 16px;
}

/* 挙動
---------------------------------------------------------------------------- */
/* hover */
input:hover,
textarea:hover,
select:hover {
  border-color: #666;
}

input[type="radio"] + span:hover,
input[type="checkbox"] + span:hover {
  color: white;
}

input[type="radio"] + span:hover::before,
input[type="checkbox"] + span:hover::before {
  border-color: #000;
}

input[type="radio"] + span:hover::after,
input[type="checkbox"] + span:hover::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #ccc;
}

/* checked */
input[type="radio"]:checked + span,
input[type="checkbox"]:checked + span {
  color: #3498db;
}

input[type="radio"]:checked + span::before,
input[type="checkbox"]:checked + span::before {
  border-color: #3498db;
}

input[type="radio"]:checked + span::after,
input[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #3498db;
}

/* radio */
input[type="radio"] + span::before,
input[type="radio"] + span:hover::after,
input[type="radio"]:checked + span::after {
  border-radius: 50%;
}

/* button */
@media screen and (min-width: 768px) {
  input[type="submit"]:hover,
  input[type="reset"]:hover,
  input[type="button"]:hover,
  button:hover {
    border-color: #3498db;
    background: #3498db;
    color: #fff;
  }
}

/* focus */
input:focus,
textarea:focus {
  border-color: #3498db;
}

input[type="submit"]:focus,
input[type=reset]:focus,
input[type="button"]:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
  background: #333333;
  color: rgba(255, 255, 255, 0.8);
  cursor: not-allowed;
}

input[type="radio"]:disabled + span,
input[type="checkbox"]:disabled + span {
  color: #ccc;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span::before,
input[type="checkbox"]:disabled + span::before {
  border-color: #ccc;
  cursor: not-allowed;
}

/* バリデーション */
/* placeholder */
*,
*::before,
*::after {
  box-sizing: border-box;
}

