/*じわりと表示*/
.u-blur {
  animation-name: blurAnime;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes blurAnime {
  from {
    filter: blur(10px);
    transform: scale(1.02);
    opacity: 0;
  }
  to {
    filter: blur(0);
    transform: scale(1);
    opacity: 1;
  }
}

.js-blurTrigger {
  opacity: 0;
}

/* ドロワーメニュークリック時、背景全体をぼかす */
.u-mainblur {
  animation: blur .6s ease-out forwards;
}

@keyframes blur {
  from {
    filter: blur(0);
  }
  to {
    filter: blur(5px);
  }
}

.u-fadeIn {
  animation-name: fadeInAnime;
  animation-duration: 2s;
  /*ゆっくり出現するため数値変更*/
  animation-fill-mode: forwards;
  opacity: 0;
}

@keyframes fadeInAnime {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*テキストがほのかに光りながら出現*/
.js-glowAnime span {
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .js-glowAnime span {
    opacity: 1;
  }
}

.js-glowAnime.u-glowAnime span {
  animation: glow_anime_on 1s ease-out forwards;
}

@keyframes glow_anime_on {
  0% {
    opacity: 0;
    text-shadow: 0 0 0 #fff,0 0 0 #fff;
  }
  20% {
    opacity: 0;
    text-shadow: 0 0 0 #fff,0 0 0 #fff;
  }
  50% {
    opacity: 1;
    text-shadow: 0 0 10px #fff,0 0 15px #fff;
  }
  90% {
    opacity: 1;
    text-shadow: 0 0 0 #fff,0 0 0 #fff;
  }
  100% {
    opacity: 1;
    text-shadow: none;
  }
}

@media screen and (min-width: 768px) {
  .u-hidden-pc {
    display: none;
