.p-top-content {
position: relative;
}

.p-top-content__inner{}

.p-top-content__wrapper {}

.p-top-content__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  @include mq() {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.p-top-content__item {
  position: relative;
}

//ホバー時、画像に20％の白を重ねる
.p-top-content__link:hover::before {
  @include mq() {
    opacity: 0.2;
  }
}

.p-top-content__link::before {
  @include mq() {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $white;
    opacity: 0;
    transition: all 0.3s;
    z-index: 2;
  }
}
.p-top-content__link::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color:rgba($color: $black, $alpha: 0.3);
    // z-index: 1;
}



//画像の比率を設定
.p-top-content__img {
  position: relative;
  width: 100%;
  padding-top: 88.88%;
  @include mq() {
    padding-top: 100%;
  }
}

.p-top-content__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.p-top-content__textblock {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  text-align: center;
  z-index: $layer-floating;
}

.p-top-content__text {
  font-size: rem(20);
  @include mq() {
    font-size: rem(25);
  }
}